.home {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(to bottom right, #38A2D7, #561139);

  & p {
    font-style: italic;
  }

  &_socials-container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    position: relative;
    top: 10px;
    padding: 0;
    width: 150px;
    height: 50px;

    & img {
        width: 30px;
        height: 30px;
    }

    & li {
        list-style: none;
    }
  }
}

.home * {
  margin: 0;
}
